@import '~assets/styles/h3Imports';

.root {
  --brand-secondary-border-color: var(
    --menu-item-separator,
    rgba(#323264, 0.16)
  );

  left: 0;
  padding: var(--spacing-s) var(--spacing-xs);
  position: absolute;
  top: calc(100% + var(--spacing-xxxs));
  width: 100%;
  z-index: 1;

  &:global(.active-search) {
    padding: var(--spacing-xs);
  }
}

.options-header {
  color: var(--gray-3, #606565);
  font-size: em(14);
  font-weight: 600;
  margin: em(8) 0;

  @media (max-width: #{$max-tablet-size}) {
    margin: 0;
  }
}

.options-header-text {
  color: var(--gray-3, #606565);
}

.options-header-container {
  border-bottom: em(1) solid var(--brand-secondary-border-color);
  padding: var(--spacing-xxxs) 0;
}

.options-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.option {
  margin: var(--spacing-xxxs) 0;

  &[aria-selected='true'] {
    & .option-button {
      border: em(1) solid var(--tertiary-button-background-pressed, #d6d6da);
    }
  }
}

.popup-footer {
  border-top: em(1) solid var(--brand-secondary-border-color);
  margin: 0;
}

.option-button {
  color: var(--gray-3);
  font-size: em(16, 16);
  font-weight: 400;
  justify-content: flex-start;
  width: 100%;
}

.all-results-link {
  font-size: em(16);
  font-weight: 600;
  text-decoration: underline;

  &:active,
  &:hover {
    --button-background-color: transparent;
  }
}

.highlighted-mark {
  background: none;
  font-weight: 700;
}

.spinner {
  --spinner-size: 20px;
}
