@import '~assets/styles/h3Imports';

.root {
  --brand-secondary-border-color: var(
    --menu-item-separator,
    rgba(#323264, 0.16)
  );

  left: 0;
  padding: var(--spacing-s) var(--spacing-xs);
  position: absolute;
  top: calc(100% + var(--spacing-xxxs));
  width: 100%;
  z-index: 1;

  &:global(.active-search) {
    padding: var(--spacing-xs);
  }
}

.options-header {
  color: var(--gray-3, #606565);
  font-size: em(14);
  font-weight: 600;
  margin: em(8) 0;

  @media (max-width: #{$max-tablet-size}) {
    margin: 0;
  }
}

.options-header-text {
  color: var(--gray-3, #606565);
}

.options-header-container {
  border-bottom: em(1) solid var(--brand-secondary-border-color);
  padding: var(--spacing-xxxs) 0;
}

.options-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.option {
  align-items: center;
  column-gap: 8px;
  cursor: pointer;
  display: flex;
  font-size: em(16, 16);
  font-weight: 400;
  margin: 0 calc(var(--spacing-xs) * -1);
  padding: var(--spacing-xxxs) var(--spacing-xs);

  &[aria-selected='true'] {
    outline: var(--focus-color, #0082aa) solid em(2);
    outline-offset: var(--focus-offset, em(-1));
  }

  &:hover {
    background-color: var(--text-button-hover-background);
  }

  &:last-child {
    border-top: em(1) solid var(--brand-secondary-border-color);
    color: var(--accent-interactive, #284b9b);
    font-weight: 600;
    margin-top: em(8);
    padding: var(--spacing-xxxs) var(--spacing-xs);
    text-decoration: underline;
  }
}

.highlighted-mark {
  background: none;
  font-weight: 700;
}

.spinner {
  --spinner-size: 20px;
}
